.banner {
	position: relative;
  background-image: url('../images/banner-bg.png');
  background-repeat: no-repeat;
  background-size: cover;
	padding-bottom: 50px;
	&-blk {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-top: 97px;
	}
	&-cnt {
		width: 100%;
		max-width: 715px;
		&-title {
			margin-bottom: 45px;
			padding-bottom: 40px;
			font-family: 'Geometria-Regular';
			font-size: 48px;
			text-transform: uppercase;
			color: #ffffff;
			border-bottom: 2px solid rgba(255, 255, 255, 0.2);
			& span {
				font-family: 'Geometria-Bold';
				font-style: italic;
				color: #fc9f6e;
			}
		}
		&-info {
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
			padding-bottom: 116px;
			& > div {
				display: flex;
				align-items: flex-start;
				justify-content: flex-start;
				& span {
					text-transform: uppercase;
					&:nth-child(1){
						display: inline-block;
						margin-top: -11px;
						margin-right: 3px;
						font-family: 'Geometria-Bold';
						white-space: nowrap;
						font-size: 56px;
						color: #fc9f6e;
					}
					&:nth-child(2){
						font-family: 'Geometria-Regular';
						font-size: 21px;
						color: #ffffff;
					}
				}
			}
		}
	}
	&-img {
		position: absolute;
    top: 0;
    right: -132px;
    width: 100%;
    max-width: 572px;
		& img {width: 100%;}
	}
	& form {
		width: 100%;
		margin-bottom: -70px;
		padding: 31px 49px 0;
    border-radius: 10px;
    background: #ffffff;
    box-shadow: 0 6px 10px rgba(128, 128, 128, 0.27);
	}
	&-form {
		&-title {
			padding-bottom: 35px;
			font-family: 'Geometria-Bold';
			text-align: center;
			font-size: 30px;
			color: #000000;
		}
		&-fields {
			display: flex;
			align-items: center;
			justify-content: space-between;
			& .btn {margin-right: 0;}
		}
	}
}

.field {
	width: 100%;
	max-width: 365px;
	height: 68px;
	margin-right: 20px;
	margin-bottom: 45px;
	padding: 5px 35px;
	font-family: 'Geometria-Regular';
	font-size: 18px;
	color: #000000;
	background: #ffffff;
	border: 1px solid #DDDDDD;
	border-radius: 35px;
}

.btn {
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	max-width: 365px;
	height: 68px;
	padding: 5px 35px;
	font-family: 'Geometria-Bold';
	font-size: 22px;
	color: #ffffff;
	background: linear-gradient(to right, #FF8D24, #FF6761);
	box-shadow: 0 10px 10px rgba(198, 94, 94, 0.3);
	border-radius: 35px;
	transition: 0.5s ease;
	&:hover {
		box-shadow: none;
	}
}

.wrapper {
  width: 100%;
	max-width: 1200px;
	padding-left: 15px;
	padding-right: 15px;
  margin: 0 auto;
}


.banner-wrapper {
  width: 100%;
  max-width: 1270px;
	padding-left: 15px;
	padding-right: 15px;
	margin: 0 auto;
	& .wrapper {padding: 0;}
}

header {
  display: flex;
  align-items: center;
	justify-content: space-between;
	padding-top: 12x;
	padding-bottom: 37px;
}

.logo {
  color: #ffffff;
  &-title {
      font-family: 'Geometria-ExtraBold';
      font-size: 35px;
      & span {
          color: #fc9f6e;
      }
  }
  &-subtitle {
      font-family: 'Geometria-Regular';
      font-size: 17px;
  }
}

.header-blk {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  max-width: 270px;
}

.lang {
  position: relative;
	margin-right: 10px;
	cursor: pointer;
  & .lang-active,
  & .lang-list-item {
      display: flex;
      align-items: center;
      justify-content: flex-start;
  }
  & span {
      margin-right: 10px;
      font-family: 'Geometria-Regular';
      font-size: 18px;
      text-transform: uppercase;
      color: #ffffff;
  }
  & img {
      width: 31px;
      height: 18px;
      border-radius: 5px;
  }
  & .lang-list {
			position: absolute;
			top: calc(100% + 5px);
			left: -5px;
			opacity: 0;
			z-index: -10;
			width: calc(100% + 10px);
			padding: 5px 4px;
			border-radius: 10px;
			background: #ffffff;
			transition: 0.5s ease;
      & span {color: #000000;}
      &-item {padding-bottom: 5px;}
	}
	&.active {
		& .lang-list {
			opacity: 1;
			z-index: 10;
		}
	}
}

.callback {
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  flex-direction: column;
	font-family: 'Geometria-Regular';
  & a:first-child {
      display: flex;
      align-items: center;
			justify-content: flex-start;
			padding-bottom: 3px;
      font-size: 18px;
      text-transform: uppercase;
      color: #ffffff;
			transition: 0.3s ease;
			&:hover {color: #fc9f6e;}
      & img {
          width: 100%;
          max-width: 13px;
          margin-right: 5px;
      }
	}
	& a:last-child {
		padding-bottom: 2px;
		font-size: 16px;
		color: #fc9f6e;
		border-bottom: 1px solid #fc9f6e;
		transition: 0.3s ease;
		&:hover {
			border-bottom: 1px solid transparent;
		}
	}
}

nav {
	position: absolute;
	top: 347px;
	right: -174px;
	z-index: 40;
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	max-width: 430px;
	transform: rotate(90deg);
	& .callback {display: none;}
	& a {
		font-family: 'Geometria-Light';
		font-size: 21px;
		color: #ffffff;
		transition: 0.5s ease;
		&:hover{color: #fc9f6e;}
		&:not(:last-child) {padding-right: 10px;}
	}
}

.left-circles {
	position: absolute;
	top: 160px;
	left: 0;
}

.title {
	padding-bottom: 33px;
	font-family: 'Geometria-Bold';
	font-size: 36px;
	text-transform: uppercase;
	color: #000000;
	& span {
		font-style: italic;
		color: #fc9f6e;
	}
	&-white {color: #ffffff;}
	&-g {
		font-family: 'Gilroy-ExtraBoldItalic';
	}
}

.best {
	padding-top: 69px;
	&-tabs {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		& button {
			display: flex;
			align-items: center;
			justify-content: center;
			transition: 0.5s linear;
		}
		& .btn,
		& .field {
			height: 58px;
			margin-bottom: 23px;
			margin-right: 0px;
		}
		& .field:not(.btn) {
			font-family: 'Geometria-Light';
			font-size: 22px;
			border:1px solid #14B6F2;
			padding: 5px;
			&:hover {
				border: 1px solid #FF8D24;
				color: #FF8D24;
			}
		}
	}
}
.card-wrapper {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	width: 100%;
	margin-bottom: 160px;
	opacity: 0;
	position: absolute;
	top: -100000px;
	&.active {
		opacity: 1;
	position: static;
	top: 1px;
	}
}
.card {
	margin-right: 30px;
	margin-top: 8px;
	&-cnt {
		width: 100%;
		max-width: 488px;
		overflow: hidden;
		margin-bottom: 15px;
		& img {
			cursor: pointer;
			width: 100%;
		}
		.slick-arrow {
			opacity: 0;
			z-index: 1;
			height: 100%;
			width: 40px;
			display: flex;
			align-items: center;
			justify-content: center;
			background: rgba(0, 0, 0, 0.5) !important;
			transition: 0.5s ease;
			&:before {
				content: '' !important;
				display: block;
				width: 17px;
				height: 29px;
				background: url(../images/arrow-slick.png) ;
				background-repeat: no-repeat;
				background-size: contain;
				opacity: 1 !important;
			}
		}
		&:hover .slick-arrow {opacity: 1;}
		.slick-prev {
			top: 0;
    	transform: translate(0);
			left: 0;
		}
		.slick-next {
			top: 0;
    	transform: translate(0);
			right: 0;
			&:before {
				transform: rotate(180deg);
			}
		}
	}
	&-nav {
		& .slick-track {
			display: flex;
			align-items: center;
			// justify-content: space-between;
			&:before {display: none;}
		}
		width: 100%;
		max-width: 490px;
		& img {
			cursor: pointer;
			width: 109px !important;
			min-width: 110px;
			max-height: 79px;
			overflow: hidden;
			margin-right: 16px;
		}
	}
	&-item {
		width: 100%;
		&-title {
			padding-bottom: 39px;
			font-family: 'Geometria-Medium';
			font-size: 30px;
		}
		&-subtitle {
			padding-bottom: 18px;
			font-family: 'Geometria-Medium';
			font-size: 24px;
		}
		&-info {
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
			& .item-price {
				padding-bottom: 20px;
				font-family: 'Geometria-Light';
				font-size: 24px;
			}
			&-left {
				max-width: 230px;
				width: 100%;
				padding-right: 15px;
			}
			&-benefits {
				width: 100%;
				max-width: 315px;
				& > div {
					display: flex;
					align-items: flex-start;
					justify-content: flex-start;
					padding-bottom: 25px;
					& > div {
						display: flex;
						align-items: center;
						justify-content: center;
						width: 100%;
						padding-top: 4px;
						max-width: 41px;
						min-width: 41px;
						margin-right: 25px;
					}
					& img {width: 100%;}
					&:nth-child(1) img {max-width: 41px;}
					&:nth-child(2) img {max-width: 38px;}
					&:nth-child(3) img {max-width: 26px;}
				}
				& p {
					font-family: 'Geometria-Light';
					font-size: 18px;
				}
			}
		}
		&-price {
			& .card-item-subtitle {padding-bottom: 10px;}
		}
		& .colors {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			padding-bottom: 43px;
			& > a {
				display: block;
				width: 34px;
				height: 34px;
				margin-right: 8px;
				transition: 0.3s ease;
				&:hover,
				&.active {box-shadow: 0 0 7px 2px #df8500;}
				&.orange {background: #FFC300;}
				&.red {background: #F24841;}
				&.green {background: #84C550;}
				&.blue {background: #7CA4D9;}
				&.black {background: #000000;}
			}
		}
		& .btn {
			display: flex;
			align-items: center;
			justify-content: center;
			height: 50px;
			max-width: 282px;
			margin-top: 30px;
			padding: 5px;
			font-size: 16px;
			text-transform: uppercase;
		}
	}
}

.test {
	padding-bottom: 77px;
	&-title {
		width: 100%;
		padding: 20px 25px;
		font-family: 'Geometria-Bold';
		font-size: 24px;
		color: #ffffff;
		background: #0079E1;
	}
	&-cnt {
		padding: 30px 30px 50px;
		&-info {
			padding-bottom: 35px;
			font-family: 'Geometria-Medium';
			font-size: 24px;
			text-align: center;
		}
		& .btn {
			max-width: 300px;
			height: 50px;
			margin: 0 auto;
			font-size: 16px;
			text-transform: uppercase;
		}
	}
	
	&-first {
		& .test-cnt {
			padding: 144px 30px 222px;
			background: url('../images/test-first.jpg');
			background-repeat: no-repeat;
			background-size: cover;
		}
	}
	&-item {box-shadow: 0 5px 23px rgba(211, 211, 211, 0.77);}
	&-step {
		& .test-cnt {padding: 32px 32px 55px;}
		& input[type="radio"] {display: none;}
		& .test-cnt-checkbox {
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
			margin-bottom: 57px;
		}
		& .checkbox {
			&-img {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 100%;
				height: 266px;
				max-width: 308px;
				margin-bottom: 17px;
				&-discount {
					&::before {
						content: '';
						position: absolute;
						top: 5px;
						right: 5px;
						display: block;
						width: 110px;
						height: 105px;
						background-image: url('../images/discount-15.png');
						background-repeat: no-repeat;
						background-size: contain;
					}
				}
				& img {
					width: 100%;
					height: 100%;
					border: 5px solid transparent;
					transition: 0.3s linear;
				}
			}
			&-name {
				font-family: 'Geometria-Medium';
				font-size: 24px;
				text-align: center;
			}
		}
		& label {
			cursor: pointer;
			&.active,
			&:hover {
				& .checkbox-img img {border: 5px solid #41BBF3; }
			}
			& input:checked + .checkbox .checkbox-img img {border: 5px solid #41BBF3; }
		}
		&-checkbox {
			& .test-cnt {padding: 160px 32px 230px;}
			&-column {
				& .test-cnt {padding: 32px 32px 55px;}
			}
			& .test-cnt-checkbox {
				display: flex;
				align-items: flex-start;
				justify-content: space-between;
				margin-bottom: 40px;
				& .checkbox {
					display: flex;
					align-items: center;
					justify-content: flex-start;
					flex-flow: row nowrap;
					&-field {
						width: 31px;
						min-width: 31px;
						height: 31px;
						margin-right: 20px;
						border: 1px solid #DDDDDD;
						border-radius: 5px;
						transition: 0.3s ease;
						&-wrap {
							display: flex;
							align-items: center;
							justify-content: flex-start;
							flex-flow: row nowrap;
							width: 100%;
							padding-left: 5px;
							&-2 {align-items: flex-start;}
						}
					}
					&-title {
						font-family: 'Geometria-Regular';
						font-size: 20px;
					}
					&-column {
						flex-direction: column;
						align-items: center;
						justify-content: flex-start;
						width: 100%;
    				max-width: 308px;
					}
				}
				& label {
					cursor: pointer;
					&:hover {
						& .checkbox-field {
							border: 1px solid #41BBF3;
						}
					}
					& input:checked + .checkbox .checkbox-field {
						border: 1px solid #41BBF3; 
						background: #41BBF3;
					}
				}
			}
		}
	}
	&-six {
		& .test-title {text-align: center;}
		& .test-cnt {
			background: url('../images/test-last.jpg');
			background-repeat: no-repeat;
			background-size: cover;
			padding: 160px 15px 235px;
		}
		& .test-cnt-info {
			padding-bottom: 45px;
		}
		& .form-end {
			display: flex;
			align-items: center;
			justify-content: center;
			& .field {
				height: 50px;
				margin: 0 40px 0 0;
				max-width: 300px;
				padding-left: 20px;
				font-family: 'Geometria-Light';
				font-size: 16px;
			}
			& .btn {
				margin: 0;
				max-width: 300px;
			}
		}
	}
	&-hidden {display: none;}
	& .btn {cursor: pointer;}
}

