/* Geometria */

@font-face {
    font-family: 'Geometria-Regular';
    src: url('../css/fonts/geometria/Geometria.woff2') format('woff2'),
        url('../css/fonts/geometria/Geometria.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Geometria-Bold';
    src: url('../css/fonts/geometria/Geometria-Bold.woff2') format('woff2'),
        url('../css/fonts/geometria/Geometria-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Geometria-Medium';
    src: url('../css/fonts/geometria/Geometria-Medium.woff2') format('woff2'),
        url('../css/fonts/geometria/Geometria-Medium.woff') format('woff');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'Geometria-Light';
    src: url('../css/fonts/geometria/Geometria-Light.woff2') format('woff2'),
        url('../css/fonts/geometria/Geometria-Light.woff') format('woff');
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: 'Geometria-ExtraBold';
    src: url('../css/fonts/geometria/Geometria-ExtraBold.woff2') format('woff2'),
        url('../css/fonts/geometria/Geometria-ExtraBold.woff') format('woff');
    font-weight: 800;
    font-style: normal;
}


@font-face {
    font-family: 'Gilroy-ExtraBoldItalic';
    src: url('../css/fonts/gilroy/Gilroy-ExtraBoldItalic.woff2') format('woff2'),
        url('../css/fonts/gilroy/Gilroy-ExtraBoldItalic.woff') format('woff');
    font-weight: 800;
    font-style: italic;
}

